/* Tooltip text */
.projtooltip .projtooltiptext {
    visibility: hidden;
    width: 260px;
    color: white;
    display: flex;
    align-items: center;
    transition: opacity .25s;
   
    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index: 1;
    left: 68%;
    margin-left: -60px;
  }
  
  /* Show the tooltip text when you mouse over the tooltip container */
  .projtooltip:hover .projtooltiptext {
    opacity: 1;
    visibility: visible;
  }
.navbar {
  z-index: 1;
}

.pin {
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  background-color: black;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block; /* If you want dots under the hoverable text */
}

.logo {
  height: 60px;
  width: 70px;
}

@media only screen and (max-width: 600px) {
  .logo {
    height: 30px;
    width: 35px;
  }
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 100px;
  background-color: rgb(104, 104, 104);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  opacity: 0;
  transition: opacity .25s;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  margin-left: -60px;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  opacity: 1;
  visibility: visible;
}

